import { Component, OnInit } from '@angular/core';
import { Inputvalue } from '../model/inputvalue';
import { Listname } from '../model/listname';
/*declare var jquery:any;
declare var $ :any;*/
@Component({
  selector: 'app-test1',
  templateUrl: './test1.component.html',
  styleUrls: ['./test1.component.css']
})
export class Test1Component implements OnInit {
  title2 = "chào mừng tất cả";
  anhien = false;
  addten = '';

  anfromaddten = true; 
  filter = "xemtatca";
  editactive : Inputvalue = {
    id: 0 ,
    name: ""
  };
  editfrom = true;
  textvaluethem = 'Click để hiện from';
  inputvalue : Inputvalue = {
    id: 0 ,
  	name: ""
  };
  inpputvalue2 = Listname;
  anhienfunction()
  {
  	this.anhien = !this.anhien;
  }
  constructor() { }
  themten()
  {
   if(this.textvaluethem=="update")
   {
     alert("ok");
   }
   else{
        if(this.anfromaddten==false)
        {     
          if(this.addten=="")
          {
            alert("Mời bạn nhập tên cần thêm");
            return false;
          }    
          this.inpputvalue2.push({
            id : this.inpputvalue2.length + 1,
            name: this.addten
          });
          this.textvaluethem = 'Click để hiện from';
          this.addten='';      
        }
        else{
          this.textvaluethem = 'Thêm Tên';
        }
        this.anfromaddten = !this.anfromaddten;
  }
  }
  xoaten(name)
  {  
    const index = this.inpputvalue2.findIndex(e => e.name  === name); 
    this.inpputvalue2.splice(index, 1);
  }
  editten(inputvalue: Inputvalue): void {    
    this.editactive = inputvalue;
    this.editfrom=false;
  }
  save()
  {
    this.editfrom = true;
  }
  ngOnInit() {
  }

}
