import { Component} from '@angular/core';
import{ Baiviet_service } from '../service/baiviet.service';
@Component({
  selector: 'app-lienhe',
  templateUrl: './lienhe.component.html',
  styleUrls: ['./lienhe.component.css'],
  providers: [Baiviet_service]
})
export class LienheComponent{
  
  constructor(private baiviet_service : Baiviet_service) { }
  onSubmit(formlienhe)
  {
   // console.log(formlienhe);
     this.baiviet_service.guimail(formlienhe.value)
     .then(res => formlienhe.reset())
     .catch(err=> console.log(err));    
    
  }
}
